<template>
    <div class="register">
        <div class="title">基本信息</div>
        <div class="rInput">
            <div class="lable">用 户 名：</div>
            <input type="text" v-model="name">
        </div>
        <div class="rInput">
            <div class="lable">密 码：</div>
            <input type="password" v-model="password">
        </div>
         <div class="title">详细信息</div>
         <div class="rInput">
            <div class="lable">姓 名：</div>
            <input type="text">
        </div>
        <div class="rInput">
            <div class="lable">证件号码：</div>
            <input type="text">
        </div>
         <div class="title">联系方式</div>
         <div class="rInput">
            <div class="lable">手机号码：</div>
            <input type="text">
        </div>
        <div class="rInput">
            <div class="lable">电子邮箱：</div>
            <input type="text">
        </div>
         <button class="toRBtn" @click="comRegister()">注 册</button>
    </div>
    
</template>
<script>
export default {
    data(){
        return {
            name:null,
            password:null,
        }
    },
    methods:{
        comRegister(){
            if(this.name&&this.password){
                window.localStorage.setItem("name",this.name.trim());
                window.localStorage.setItem("password",this.password);
                 this.$notify.success('注册成功');
                this.$router.push('/login');
            }else{
                   this.$notify.danger('用户名和密码不能为空');
            }
        }
    }
}
</script>
<style scoped>
.register{
    display: flex;
    flex-direction: column;
    height: 650px;
    background: #F5F5F5;
}
.rInput{
    display: flex;
    padding: 12px 2vw;
    background-color: #fff;
    border-bottom: 1px solid #9C9C9C;

}
input{
    flex-grow: 1;
    font-size: 1.3em;
    font-weight: 500;
}
.lable{
    width: 100px;
    font-size: 1.5em;
    text-align: right;
}
.title{
    background-color: #F5F5F5;
    padding: 10px;
    font-size: 1.6em;
    border-bottom: 1px solid #9C9C9C;

}
.toRBtn{
    color: #fff;
    background-color: #409eff;
    border-color: #409eff;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    border: 1px solid #dcdfe6;
    -webkit-appearance: none;
    text-align: center;
    outline: none;
    margin: 0;
    transition: .1s;
    font-weight: 500;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    padding: 12px 20px;
    font-size: 14px;
    border-radius: 4px;
    margin-top: 15px;
}
</style>